<template>
  <v-container fluid>
    <!--卡片-->
    <v-row justify="center">
      <v-col sm="6" md="4" lg="4">
        <v-card class="mx-auto" elevation="10" color="rgba(255,255,255,0.5)">
          <v-img
            class="white--text"
            height="200px"
            src="../../assets/user/top2.gif"
          >
            <v-app-bar flat color="rgba(0, 0, 0, 0)">
              <v-app-bar-nav-icon color="black"></v-app-bar-nav-icon>

              <v-toolbar-title class="text-h5 black--text pl-0">
                Register
              </v-toolbar-title>
            </v-app-bar>
          </v-img>
          <!--表单填写部分-->
          <v-card-text>
            <v-form
              ref="registerForm"
              v-model="valid"
              lazy-validation
              autocomplete="off"
            >
              <!--用户名-->
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="form.username"
                    :rules="rules.usernameRules"
                    label="USER NAME"
                    @blur="usernameBlur"
                  ></v-text-field>
                </v-col>
                <v-col>
                  <v-text-field
                    v-model="form.email"
                    :rules="rules.emailRules"
                    label="EMAIL"
                  ></v-text-field>
                </v-col>
              </v-row>
              <!--密码-->
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="form.password"
                    :rules="rules.passwordRules"
                    label="PASSWORD"
                    :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                    @click:append="show = !show"
                    :type="show ? 'text' : 'password'"
                  ></v-text-field>
                </v-col>
              </v-row>
              <!--确认密码-->
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="form.passwordConfirm"
                    :rules="[
                      rules.passwordConfirmRules.required,
                      rules.passwordConfirmRules.equal(
                        form.password,
                        form.passwordConfirm
                      ),
                    ]"
                    label="CONFIRM YOUR PASSWORD"
                    :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                    @click:append="show = !show"
                    :type="show ? 'text' : 'password'"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <!--动作按钮-->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              :disabled="!valid"
              depressed
              elevation="8"
              @click="register()"
              >Register</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <!--错误提示，用于反馈创建用户时的错误-->
    <v-snackbar v-model="snackbar">
      {{ snackbarData }}
      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
export default {
  name: "Register",
  data: () => ({
    // 用于验证表单内容填写是否正确
    valid: true,
    // 用于控制密码是否以可见的形式显示
    show: false,
    // 用于控制消息条是否显示
    snackbar: false,
    // 表单填写的内容
    snackbarData: "",
    form: {
      username: "",
      email: "",
      password: "",
      passwordConfirm: "",
    },
    // 验证规则
    rules: {
      usernameRules: [
        (v) => !!v || "用户名不能为空",
        (v) =>
          ((v || "").length >= 3 && (v || "").length <= 20) ||
          "用户名必须在3-20个字符以内",
        (v) => /^[_a-zA-Z0-9]+$/.test(v) || "密码必须由英文字母和下划线组成",
      ],
      emailRules: [
        (v) =>
          /^([a-zA-Z0-9]+[-_\.]?)+@[a-zA-Z0-9]+\.[a-z]+$/.test(v) ||
          "请输入正确的邮箱",
      ],
      passwordRules: [
        (v) => !!v || "密码不能为空",
        (v) =>
          ((v || "").length >= 6 && (v || "").length <= 20) ||
          "密码必须在6-20个字符以内",
        (v) => /^[_a-zA-Z0-9]+$/.test(v) || "密码必须由英文字母和下划线组成",
      ],
      passwordConfirmRules: {
        required: (v) => !!v || "请输入确认密码",
        equal: (password, passwordConfirm) =>
          password === passwordConfirm || "两次密码不一致",
      },
    },
  }),
  methods: {
    // 注册功能
    register() {
      // 先验证表单填写的正确性

      // axios发送post请求
      this.axios
        .post(this.targetIP() + "/verify.register", {
          username: this.form.username,
          email: this.form.email,
          password: this.form.password,
          //尝试隐藏请求表单数据
        })
        .then((response) => {
          console.log(response);
          console.log(response.data);
          if (response.data == "success") {
            
            // 注册成功，跳转到登录页面
            this.$router.push({
              name: "Login",
            });
          } else {
            this.snackbarData = response.data;
            this.snackbar = true;
          }
        })
        .catch((error) => {
          // 发生请求错误，错误可能是：1.因为用户名已存在 2.提交的数据不符合要求 3.服务器内部错误
          console.log(error);
          // 显示错误消息
          this.snackbarData = "与服务器连接发生了位置错误";
          this.snackbar = true;
        });
    },
    usernameBlur() {
      // axios发送post请求
      this.axios
        .post(this.targetIP() + "/verify.register/checkDuplicate", {
          username: this.form.username,
        })
        .then((response) => {
          console.log(response);
          console.log(response.data);
          if (response.data == false) {
            this.snackbarData = "用户名已存在，请再想一个";
            this.snackbar = true;
            this.form.username = "";
          } else if (response.data == "methoderror") {
            this.snackbarData = "MethodError";
            this.snackbar = true;
          } else if (response.data == true) {
          }
        })
        .catch((error) => {
          // 发生请求错误，错误可能是：1.因为用户名已存在 2.提交的数据不符合要求 3.服务器内部错误
          console.log(error);
          // 显示错误消息
          this.snackbarData = "与服务器连接发生了位置错误";
          this.snackbar = true;
        });
    },
    targetIP() {
      console.log(this.$store.state.targetIP);
      return this.$store.state.targetIP;
    },
  },
};
</script>
